.section {
  padding: 30px 0; }
  .section--inverse {
    background: #19354a; }

.gallery__sizer {
  width: 8.33333%;
  clear: both; }

.gallery {
  width: 93%;
  margin: auto;
  margin-top: 50px;
  margin-bottom: 50px; }
  .gallery__title {
    font-family: Arial, Helvetica, sans-serif; }
  .gallery__controls__button {
    background: none;
    border: 1px solid #333;
    padding: 10px;
    font-family: 'Courier New', Courier, monospace;
    border-radius: 1px;
    margin-top: 8px; }
    .gallery__controls__button:hover {
      background: #eee; }
  .gallery__items {
    margin-top: 20px; }
    @media (min-width: 768px) {
      .gallery__items {
        margin-left: -25px;
        margin-right: -25px; } }
  .gallery__item {
    position: relative;
    float: left;
    min-height: 1px;
    padding: 0;
    margin: 25px 0;
    box-sizing: border-box;
    width: 100%; }
    .gallery__item:hover {
      opacity: 0.8 !important; }
    @media (min-width: 768px) {
      .gallery__item {
        width: calc(50% - 50px);
        margin: 25px; } }
    @media (min-width: 992px) {
      .gallery__item {
        width: calc(33.33% - 50px);
        margin: 25px; } }
    .gallery__item__image {
      border: 1px solid #cccccc;
      background: #eee;
      height: 100%;
      width: calc(100% - 2px);
      display: flex;
      justify-content: center;
      align-items: center; }
      .gallery__item__image__spacer {
        display: block;
        position: relative;
        width: 0;
        height: 0;
        padding-bottom: 50%; }
      .gallery__item__image__inner {
        width: 100%;
        object-fit: cover; }
    .gallery__item__info {
      margin-top: 15px;
      display: flex;
      justify-content: space-between;
      align-items: center;
      flex-flow: row wrap; }
      .gallery__item__info__heading {
        font-family: 'Courier New', Courier, monospace;
        display: inline-block;
        font-size: 16px;
        line-height: 20px;
        margin-right: 10px;
        color: #333; }
      .gallery__item__info__subheading {
        font-family: 'Courier New', Courier, monospace;
        display: inline-block;
        font-size: 16px;
        line-height: 16px;
        color: #555;
        font-style: italic; }
      .gallery__item__info__description {
        font-family: 'Courier New', Courier, monospace;
        display: inline-block;
        width: 100%;
        font-size: 16px;
        line-height: 20px;
        color: #555;
        font-weight: 300; }
    .gallery__item__link {
      position: absolute;
      width: 100%;
      height: 100%;
      top: 0;
      left: 0; }
    .gallery__item--no-image {
      border: 1px solid #cccccc; }
      .gallery__item--no-image:hover {
        opacity: 1 !important;
        background-color: #f2f2f2; }
      .gallery__item--no-image::before {
        content: '';
        display: block;
        position: relative;
        width: 0;
        height: 0;
        padding-bottom: 50%; }
      .gallery__item--no-image .gallery__item__info {
        padding: 0 10%;
        box-sizing: border-box;
        position: absolute;
        height: 100%;
        width: 100%;
        flex-direction: column;
        justify-content: center;
        top: 0;
        margin: 0; }
        .gallery__item--no-image .gallery__item__info__heading, .gallery__item--no-image .gallery__item__info__subheading, .gallery__item--no-image .gallery__item__info__description {
          text-align: center;
          margin-right: 0;
          display: inline-block;
          width: 100%; }
  .gallery--inverse * {
    color: #EEE; }
  .gallery--inverse .gallery__controls__button {
    border: 1px solid #CCC; }
    .gallery--inverse .gallery__controls__button:hover {
      background: #10222f; }
  .gallery--inverse .gallery__item--no-image {
    border: 1px solid #2c5e83; }
    .gallery--inverse .gallery__item--no-image:hover {
      opacity: 1 !important;
      background-color: #224865; }
  .gallery--inverse .gallery__item__image {
    background: #eee; }
  .gallery--inverse .gallery__item__info__heading {
    color: #DDD; }
  .gallery--inverse .gallery__item__info__subheading {
    color: #AAA; }
  .gallery--inverse .gallery__item__info__description {
    color: #AAA; }
  .gallery .gallery__item--web-assembly-logo .gallery__item__image {
    background: #654ff0; }
  .gallery .gallery__item--web-assembly-logo .gallery__item__image__inner {
    width: 60%; }

@media (min-width: 768px) {
  .site-main .inner.posts {
    overflow-x: visible; }
  article.post-card {
    transition: all 0.1s ease-out; }
    article.post-card:hover {
      transform: scale(1.1); } }

article.post-card.post.featured > a {
  border: 8px solid #ff9f60; }
  article.post-card.post.featured > a:hover {
    text-decoration: none; }

article.post-card.post.featured .featured-label {
  display: flex;
  align-items: center;
  width: 100%;
  background: #ff9f60;
  color: white;
  font-size: 16px;
  font-family: "Courier New", Courier, monospace;
  font-weight: 700;
  line-height: 1;
  padding-top: 10px; }
  article.post-card.post.featured .featured-label .icon {
    position: relative;
    top: -2px;
    width: 16px;
    max-width: 16px;
    max-height: 16px;
    margin-right: 6px; }
    article.post-card.post.featured .featured-label .icon svg {
      width: 16px;
      height: 16px; }
